      for (let key in scannetIds) {
        let videoFileName = key + ".mp4"
        let id = key.substring(0, 19)
        let instanceID = key.substring(24)
        let imgFileNamePrefix = "https://aspis.cmpt.sfu.ca/projects/mirrors/mask_verification/scannet/data/" + id + "/" + instanceID

        let bgr = instanceID.split("_")
        let finalInstanceID = "#"
        for (let i = 0; i < 3; i++) {
          let temp = Number(bgr[i]).toString(16)
          if (temp.length === 1) {
            temp = '0' + temp
          }
          finalInstanceID += temp
        }
        this.desserts.push({
          uni_id: key,
          id: id,
          label: scannetIds[key],
          instanceID: finalInstanceID,
          coarseOverlay: imgFileNamePrefix + "_coarse_instance.png",
          preciseOverlay: imgFileNamePrefix + "_detailed_instance.png",
          video1: "https://aspis.cmpt.sfu.ca/projects/mirrors/Mirror3D_final/scannet/with_mirror/precise/hole_refined_ply/video_front/" + videoFileName,
          video2: "https://aspis.cmpt.sfu.ca/projects/mirrors/Mirror3D_final/scannet/with_mirror/precise/hole_refined_ply/video_topdown/" + videoFileName,
        })

