  mounted() {
    this.loadMp3dFile();
    let videoList = document.getElementsByTagName("video");
    for (let video of videoList) {
      video.playbackRate = this.videoPlaySpeed
    }
    this.getImgHeight()
    this.isTableLoading = false
  },
  created() {
    window.addEventListener('resize', this.getImgHeight)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.getImgHeight)
  },
  watch: {
    videoPlaySpeed: function (val) {
      let videoList = document.getElementsByTagName("video");
      for (let video of videoList) {
        video.playbackRate = val
      }
    },
    selectedDataset: function (val, oldVal) {
      if (oldVal === val) {
        return
      }
      this.isTableLoading = true
      if (val === "Matterport3D") {
        this.loadMp3dFile()
      } else if (val === "NYUv2") {
        this.loadNyuFile()
      } else if (val === "ScanNet") {
        this.loadScannetFile()
      }
      this.isTableLoading = false
    }
  }
